*, html {
  margin: 0;
  padding: 0;
  font-family: Roboto,sans-serif; }

*, *:after, *:before {
  -webkit-box-sizing: border-box;
          box-sizing: border-box; }

body {
  -webkit-transition: background-color 200ms linear;
  -o-transition: background-color 200ms linear;
  transition: background-color 200ms linear;
  overflow-y: scroll;
  width: 100%; }

body, html, #root {
  padding: 0;
  margin: 0;
  min-height: 100vh; }

h3 {
  font-weight: 100;
  font-size: 30px; }

p {
  font-weight: 100; }

.main-content {
  margin: 5% 0 2% 50px; }

@media only screen and (max-width: 500px) {
  .main-content--firstPart {
    width: 100%;
    padding-top: 20%; }

  .main-content {
    margin: 0; } }
.btn-cont .btn {
  position: relative;
  padding: 13px 13px;
  margin-bottom: 50px;
  background-color: rgba(255, 255, 255, 0.16);
  border: 1px solid white;
  cursor: pointer;
  color: white;
  text-decoration: none;
  font-size: 1em;
  font-family: 'Open Sans', sans-serif;
  -webkit-font-smoothing: antialiased; }

.btn-cont .btn:hover {
  border: none; }

.btn-cont .btn:hover .line-1 {
  -webkit-animation: move1 1500ms infinite ease;
  animation: move1 1500ms infinite ease; }

.btn-cont .btn:hover .line-2 {
  -webkit-animation: move2 1500ms infinite ease;
  animation: move2 1500ms infinite ease; }

.btn-cont .btn:hover .line-3 {
  -webkit-animation: move3 1500ms infinite ease;
  animation: move3 1500ms infinite ease; }

.btn-cont .btn:hover .line-4 {
  -webkit-animation: move4 1500ms infinite ease;
  animation: move4 1500ms infinite ease; }

.btn-cont .line-1 {
  content: "";
  display: block;
  position: absolute;
  width: 1px;
  background-color: white;
  left: 0;
  bottom: 0; }

.btn-cont .line-2 {
  content: "";
  display: block;
  position: absolute;
  height: 1px;
  background-color: white;
  left: 0;
  top: 0; }

.btn-cont .line-3 {
  content: "";
  display: block;
  position: absolute;
  width: 1px;
  background-color: white;
  right: 0;
  top: 0; }

.btn-cont .line-4 {
  content: "";
  display: block;
  position: absolute;
  height: 1px;
  background-color: white;
  right: 0;
  bottom: 0; }

@-webkit-keyframes move1 {
  0% {
    height: 100%;
    bottom: 0; }
  54% {
    height: 0;
    bottom: 100%; }
  55% {
    height: 0;
    bottom: 0; }
  100% {
    height: 100%;
    bottom: 0; } }
@keyframes move1 {
  0% {
    height: 100%;
    bottom: 0; }
  54% {
    height: 0;
    bottom: 100%; }
  55% {
    height: 0;
    bottom: 0; }
  100% {
    height: 100%;
    bottom: 0; } }
@-webkit-keyframes move2 {
  0% {
    width: 0;
    left: 0; }
  50% {
    width: 100%;
    left: 0; }
  100% {
    width: 0;
    left: 100%; } }
@keyframes move2 {
  0% {
    width: 0;
    left: 0; }
  50% {
    width: 100%;
    left: 0; }
  100% {
    width: 0;
    left: 100%; } }
@-webkit-keyframes move3 {
  0% {
    height: 100%;
    top: 0; }
  54% {
    height: 0;
    top: 100%; }
  55% {
    height: 0;
    top: 0; }
  100% {
    height: 100%;
    top: 0; } }
@keyframes move3 {
  0% {
    height: 100%;
    top: 0; }
  54% {
    height: 0;
    top: 100%; }
  55% {
    height: 0;
    top: 0; }
  100% {
    height: 100%;
    top: 0; } }
@-webkit-keyframes move4 {
  0% {
    width: 0;
    right: 0; }
  55% {
    width: 100%;
    right: 0; }
  100% {
    width: 0;
    right: 100%; } }
@keyframes move4 {
  0% {
    width: 0;
    right: 0; }
  55% {
    width: 100%;
    right: 0; }
  100% {
    width: 0;
    right: 100%; } }



