@-webkit-keyframes ani-mouse {
  0% {
      opacity: 1;
      top: 29%;
  }
  15% {
      opacity: 1;
      top: 50%;
  }
  50% {
      opacity: 0;
      top: 50%;
  }
  100% {
      opacity: 0;
      top: 29%;
  }
}

@keyframes ani-mouse {
  0% {
      opacity: 1;
      top: 29%;
  }
  15% {
      opacity: 1;
      top: 50%;
  }
  50% {
      opacity: 0;
      top: 50%;
  }
  100% {
      opacity: 0;
      top: 29%;
  }
}

.scroll-btn {
  display: none;
  text-align: center;
  margin-top: 50px;
}

@media (max-width: 768px) {
  .scroll-btn {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: center;
        justify-content: center;
  }
}

.scroll-btn > * {
  display: inline-block;
  line-height: 18px;
  font-size: 13px;
  font-weight: normal;
  color: #7f8c8d;
  color: #ffffff;
  font-family: "proxima-nova", "Helvetica Neue", Helvetica, Arial, sans-serif;
  letter-spacing: 2px;
}

.scroll-btn > *:hover,
.scroll-btn > *:focus,
.scroll-btn > *.active {
  color: #ffffff;
}

.scroll-btn > *:hover,
.scroll-btn > *:focus,
.scroll-btn > *:active,
.scroll-btn > *.active {
  opacity: 0.8;
  filter: alpha(opacity=80);
}

.scroll-btn .mouse {
  position: relative;
  display: block;
  width: 35px;
  height: 55px;
  margin: 0 auto 20px;
  -webkit-box-sizing: border-box;
  box-sizing: border-box;
  border: 2px solid white;
  border-radius: 23px;
}

.scroll-btn .mouse > * {
  position: absolute;
  display: block;
  top: 29%;
  left: 50%;
  width: 8px;
  height: 8px;
  margin: -4px 0 0 -4px;
  background: white;
  border-radius: 50%;
  -webkit-animation: ani-mouse 2.5s linear infinite;
  animation: ani-mouse 2.5s linear infinite;
}


